/* Global Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f6f8fa; /* Light gray background */
  color: #24292e; /* Dark text color */
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensures the body occupies full height */
}

/* Header Styles */
header {
  background-color: #24292e; /* Navbar background similar to GitHub */
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Logo Styles */
.logo-container .logo {
  height: 40px; /* Adjust logo size */
  margin-right: 20px; /* Margin between logo and first navigation link */
}

/* Navigation Styles */
nav {
  flex-grow: 1; /* Allow nav to take available space */
}

.nav-list {
  list-style-type: none; /* Remove default list styles */
  padding: 0;
  margin: 0;
  display: flex; /* Flexbox for horizontal layout */
}

.nav-list li {
  margin-right: 20px; /* Space between links */
}

.nav-list li a {
  text-decoration: none; /* Remove underline */
  color: white; /* White text color for links */
  font-weight: bold; /* Bold links */
  transition: color 0.3s ease; /* Smooth transition for hover effect */
}

.nav-list li a:hover {
  color: #58a6ff; /* Change link color on hover */
}

/* Main Content Styles */
main {
  padding: 20px;
  flex-grow: 1; /* Allow main content to grow and push footer down */
}

/* Footer Styles */
/* Footer Styles */
footer {
  text-align: center;
  padding: 10px 0;
  background-color: #f6f8fa; /* Light gray background */
  color: #586069; /* Gray text color */
  border-top: 1px solid #e1e4e8; /* Top border for separation */
  position: fixed; /* Fixes the footer to the bottom of the viewport */
  left: 0;
  right: 0;
  bottom: 0; /* Positions the footer at the bottom */
}

body {
  background-color: #f5f5f5;
  line-height: 1.6;
  color: #333;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

.team-header {
  text-align: center;
  margin-bottom: 50px;
}

.team-header h1 {
  font-size: 2.5em;
  color: #2c3e50;
  margin-bottom: 10px;
}

.profile-card {
  background: white;
  border-radius: 10px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.profile-card:hover {
  transform: translateY(-5px);
}

.profile-header {
  border-bottom: 2px solid #eee;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.profile-header h2 {
  color: #2c3e50;
  font-size: 1.8em;
  margin-bottom: 5px;
}

.profile-header h3 {
  color: #e74c3c;
  font-size: 1.2em;
}

.profile-content p {
  margin-bottom: 15px;
  color: #666;
}

.highlight {
  color: #2c3e50;
  font-weight: 600;
}